import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import goodsCard from '@/components/goodsCard'
export default {
  data() {
    return {
      swiperOption: {
        loop:true,
         autoplay: {
          delay:3000
         }
        }
    }
  },
  components: {
    swiper,
    swiperSlide,
    goodsCard
  },
  computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    },
  created() {
    setTimeout(() => { this.$progress.hide() }, 0);
  },
  beforeCreate() {}
};




